@import '../init/customcolors';
@import '../init/custom-mixins';
// sass-lint:disable no-vendor-prefixes

// Use the saner border-box model for all elements.
* {
  box-sizing: border-box;
}

html {
  @include define-type-sizing();
  // Change the default font family in all browsers (opinionated).
  @include typeface(body);
  // Prevent adjustments of font size after orientation changes in IE and iOS.
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  // On short pages, we want any background gradients to fill the entire height
  // of the browser.
  min-height: 100%;
  // background-color: $ctvwhite;
  background-image: url('/sites/all/themes/ctvenglishtheme2prod/images-source/abstract-geometric-pattern-background.jpg');
}

body {
  // Output a horizontal grid to help with debugging typography. The
  // $typey-debug variable will toggle its output.
  @include typey-debug-grid();
  // Remove default margin and padding.
  margin: 0;
  padding: 0;
  color: color(text);
  background-color: rgba(245, 245, 245, 0.5);
  font-family: 'Roboto', sans-serif;
  opacity: 1;
  overflow-x: initial;
  font-size: 16px;
}
